import React from 'react'
import PropTypes from 'prop-types'
import { pure } from 'recompose'
// import { push } from 'react-router-redux'
import { connect } from 'react-redux'
import { createStructuredSelector } from 'reselect'

import modules from '../modules'

import ThumbnailContainer from '../components/ThumbnailContainer'
import VideoSampleMock from '../components/VideoSampleMock'
import PlayerWrapper from '../components/PlayerWrapper'

const Home = ({ togglePlayer, isPlayerOpen }) => (
  <div style={{ display: 'flex', height: '100%', flexDirection: 'column' }}>
    <ThumbnailContainer header="Today's Content">
      <VideoSampleMock onClick={togglePlayer} />
    </ThumbnailContainer>
    <ThumbnailContainer header="Yesterday's Content">
      <VideoSampleMock />
    </ThumbnailContainer>
    <PlayerWrapper isVisible={isPlayerOpen} onCloseClicked={togglePlayer} />
  </div>
)

Home.propTypes = {
  isPlayerOpen: PropTypes.bool.isRequired,
  togglePlayer: PropTypes.func.isRequired,
}

const mapStateToProps = createStructuredSelector({
  isPlayerOpen: modules.player.selectors.isPlayerOpen
})

const mapDispatchToProps = {
  togglePlayer: modules.player.actions.togglePlayer
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(pure(Home))



