import React from 'react'
import PropTypes from 'prop-types'
import { pure } from 'recompose'
import './ThumbnailContainer.css'

const ThumbnailContainer = ({ header, children }) => (
  <div className="thumbnailContainer">
    <p>{header}</p>
    <div className="thumbnailContainer_content">
      {children}
    </div>
  </div>
)

// TODO: check this
ThumbnailContainer.propTypes = {
  header: PropTypes.string,
  children: PropTypes.node.isRequired
}

ThumbnailContainer.defaultProps = {
  header: ''
}


export default pure(ThumbnailContainer)



