import MODULE_NAME from './playerConstants'

/**
 * Actions constants
 */

export const TOGGLE_PLAYER = `${MODULE_NAME}/TOGGLE_PLAYER`
export const RESET_PLAYER_REDUCER_STATE = `${MODULE_NAME}/RESET_PLAYER_REDUCER_STATE`

/**
 * Action creators
 */

export const togglePlayer = () => ({
  type: TOGGLE_PLAYER
})

export const resetPlayerReducer = () => ({
  type: RESET_PLAYER_REDUCER_STATE,
})

